<template>
  <div class="container">
    <div class="cell-box">
      <div class="cell" v-for="item in list" :key="item.title">
        <div class="title">
          {{ item.title }}
        </div>
        <div class="content">
          <img
            src="/static/user/avatar.png"
            alt=""
            class="avatar"
            v-if="item.title == '头像'"
          />
          <span v-if="item.title != '头像'" class="value">{{
            item.value
          }}</span>
          <van-icon
            name="arrow"
            color="rgba(216, 216, 216)"
            style="margin-left: 12rpx;"
          />
        </div>
      </div>
    </div>
    <div class="logout">
      退出当前账号
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      list: [
        {
          title: "头像",
          url: "",
        },
        {
          title: "用户名",
          url: "",
          value: "陈呆呆",
        },
        {
          title: "账号密码",
          url: "",
          value: "修改",
        },
        {
          title: "性别",
          url: "",
          value: "未选择",
        },
        {
          title: "手机号",
          url: "",
          value: "+66 185****5420",
        },
        {
          title: "微信",
          url: "",
          value: "解绑",
        },
        {
          title: "Apple ID",
          url: "",
          value: "绑定",
        },
        {
          title: "Facebook",
          url: "",
          value: "绑定",
        },
        {
          title: "Google",
          url: "",
          value: "绑定",
        },
      ],
    };
  },
  computed: {},
  methods: {
    goDetail() {
      wx.navigateTo({ url: "/pages/meMessage/main" });
    },
  },
};
</script>

<style lang="scss" scoped>
.container {
  margin-top: 24rpx;
}
.cell-box {
  background: white;
  .cell {
    height: 120rpx;
    background: rgba(255, 255, 255, 1);
    border-bottom: 2rpx solid rgba(241, 242, 243, 1);
    margin: 0 24rpx;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .title {
      font-size: 28rpx;
      font-family: PingFangSC-Regular, PingFang SC;
      font-weight: 400;
      color: rgba(34, 34, 34, 1);
    }
    .content {
      display: flex;
      justify-content: center;
      align-items: center;
      .avatar {
        width: 80rpx;
        height: 80rpx;
      }
      .value {
        font-size: 28rpx;
        font-family: PingFangSC-Regular, PingFang SC;
        font-weight: 400;
        color: rgba(153, 153, 153, 1);
      }
    }
  }
}
.logout {
  height: 100rpx;
  background: rgba(255, 255, 255, 1);
  border-top: 4rpx solid rgba(241, 242, 243, 1);
  line-height: 100rpx;
  text-align: center;
  font-size: 28rpx;
  font-family: PingFangSC-Regular, PingFang SC;
  font-weight: 400;
  color: rgba(255, 111, 99, 1);
}
</style>



